html * {
	box-sizing: border-box;
}

#wrapper {
	position: relative;
	overflow: hidden;
}

body {
	min-width: 320px;
	font-weight: 400;
	font-size: 16px;
	color: $gray;
	font-family: $font-pm;
}

a {
	text-decoration: none;
	color: $gray2;
	transition: all 0.3s ease;
	&:hover {
		text-decoration: none;
		transition: all 0.3s ease;
	}
}

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	margin: 0;
	font-weight: 700;
	font-size: 23px;
	font-style: normal;
	color: $gray;
}

h1 {
	font-size: 35px;
	font-family: 'MyriadPro', 'Arial', sans-serif;
}


img {
	max-width: 100%;
}

p {
	margin: 0 0 10px;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.content {
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		box-shadow: 0px 6px 24px 0px rgba(0, 0, 0, 0.06);
	}
}

.container {
	max-width: 966px;
	margin: 0 auto;
	padding: 0 15px;
}

.logo {
	display: block;
	width: 116px;

	img {
		display: block;
		width: 100%;
	}
}

.profile-description {
	width: calc(100% - 116px);
	padding-left: 7px;

	h1 {
		font-size: 23px;
		margin-bottom: 10px;
	}

	.title-holder {
		margin-bottom: 10px;
	}

	.date-info {
		border: 1px solid $blue;
		border-radius: 4px;
		padding: 5px;
		font-size: 13px;
		font-family: $font-hn;
		color: $blue;
	}

}

.statisticks-list {
	display: flex;
	align-items: center;
	font-size: 13px;
	color: $gray2;
	margin: 0 -5px 13px;

	li {
		padding: 0 5px;

		a {
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;
			align-items: center;

			&:hover {
				color: $blue;
			}
		}
	}

}

.profile-holder {
	display: flex;
	margin-bottom: 20px;
	padding-top: 10px;
}

.date-form {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;

	.date-box {
		width: calc(100% - 50px);
		max-width: 174px;
		margin: 0 5px 5px;
		position: relative;
		border: 1px solid#cccccc;
		border-radius: 4px;
		overflow: hidden;

		input {
			border: 0;
			height: 20px;
			position: relative;
		}
	}

	.date-icon {
		font-size: 14px;
		position: absolute;
		top: 0;
		right: 0;
		background-color: #ebebeb;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		border-left: 1px solid #cccccc;

		&:hover {
			color: $blue;
			background-color: #b7cfe6;
		}
	}

	.btn-close {
		right: 20px;
	}
	
}

.datepicker {
	font-size: 11px;
	width: 130px;
	color: #333333;
}

.datepicker--nav-title {
	padding: 0;
	font-family: $font-hn;
	font-weight: 700;
}

.datepicker--nav {
	padding: 0;
	font-size: 11px;
	border-bottom: 0;
}

.datepicker--day-name {
	color: #333333;
}

.datepicker--cell {
	height: 25px;
}

.content {
	padding: 35px 0 10px;
	background-color: #fafafa;
}

.content-area {
	margin: 0 -5px;

	.column {
		padding: 0 5px 10px;
	}

	.post {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		height: 292px;
		color: $white;
		padding: 0 17px;
		display: flex;
		align-items: flex-end;
		font-weight: 600;
		position: relative;

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($black, 0.5);
		}

		.camera {
			background-repeat: no-repeat;
			background-position: center;
			width: 37px;
			height: 27px;
			position: absolute;
			top: 15px;
			right: 12px;
		}

		.post-box {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 100%;

		}
	}
}

.story-stat {
	width: 100%;
	margin-bottom: 15px;
	font-weight: 700;
	font-size: 15px;
	font-family: $font-pm;

	td {
		padding: 0 5px 5px 0;

		&:first-child {
			width: 35%;
			font-weight: 600;
		}
	}

	a {
		color: $white;
		position: relative;
		padding-left: 20px;

		&:hover {
			color: $blue;
		}

		i {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
		}
	}
}

.btn-box {
	text-align: center;
	padding-bottom: 10px;
}

.button {
	font-size: 12px;
	border: 1px solid $black;
	border-radius: 18px;
	text-transform: uppercase;
	text-align: center;
	display: inline-block;
	padding: 9px 27px;

	&:hover {
		color: $blue;
		border-color: $blue;
	}
}

@media (min-width: 576px) {
	.profile-description {
		padding-left: 28px;

		h1 {
			padding-right: 19px;
		}

		.title-holder {
			display: flex;
			align-items: center;
		}
	}

	.date-form {
		justify-content: flex-start;
	}

	.statisticks-list {
		font-size: 15px;
		margin: 0 -15px 23px;

		li {
			padding: 0 15px;

			a {
				display: inline;
			}
		}
	}

	.content-area {
		display: flex;
		flex-wrap: wrap;

		.column {
			width: 50%;
		}
	}
}

@media (min-width: 768px) {

	.datepicker {
		font-size: 14px;
		width: 250px;
	}

	.datepicker--nav {
		padding: 4px;
	}

	.datepicker--nav-title{
		padding: 0 8px;
	}

	.datepicker--cell {
		height: 32px;
	}


	.profile-holder {
		align-items: flex-end;
		padding: 15px 0 0 69px;
	}

	.logo {
		width: 161px;
	}

	.profile-description {
		width: calc(100% - 161px);
		padding-left: 70px;

		h1 {
			font-size: 35px;
			padding-right: 19px;
		}

		.title-holder {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
		}

		.date-info {
			font-size: 15px;
		}
	}

	.statisticks-list {
		font-size: 17px;

		.counter {
			padding-right: 5px;
		}
	}

	.content {
		padding: 78px 0 65px;
	}

	.content-area {
			margin: 0 -15px;

		.column {
			padding: 0 15px 30px;
		}
	}

	.story-stat {
		font-size: 17px;
	}

	.date-form {
		align-items: center;
		

		label {
			width: 40px;
		}

		.date-box {
			margin: 0 10px 5px;

			input {
				height: 32px;
			}
		}

		.date-icon {
			font-size: 16px;
			width: 32px;
			height: 32px;
		}

		.btn-close {
			right: 32px;
		}
	}
}

@media (min-width: 992px) {
	.content-area {

		.column {
			width: 33.33%;
		}
	}

}
